<script>
import { buildUrlWithObj } from '@/common/utils/url-utils';
import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters('routerStore', ['permCodes']),
    devMode() {
      return process.env.NODE_ENV === 'development';
    },
    // 处理页面响应式布局
    colXL() {
      return 24 / 4;
    },
    colSmall() {
      return 24 / 3;
    },
    colOptions() {
      const { colXL, colSmall } = this;
      return {
        xs: colSmall * 1,
        sm: colSmall * 1,
        md: colSmall * 1,
        lg: colSmall * 1,
        xl: colXL * 1,
      };
    },
  },
  methods: {
    hasPerm(inputPermCode) {
      return this.permCodes && this.permCodes.some((permCode) => permCode == inputPermCode);
    },
    checkConfigRouteType(type) {
      const path = this.$route.path;
      const regExp = /\/config\/(.+?)\//;
      const match = path.match(regExp);
      if (match) {
        return match[1] === type;
      }
      return false;
    },
    getConfigRouteType() {
      const path = this.$route.path;
      const regExp = /\/config\/(.+?)\//;
      const match = path.match(regExp);
      if (match) {
        return match[1];
      }
      return null;
    },
    openNewWindow(opt) {
      let openHref = '';
      if (/^\//.test(opt.path)) {
        openHref = `#${buildUrlWithObj(opt.path, { ...opt.query })}`;
      } else {
        let { href } = this.$router.resolve({ path: opt.path, query: { ...opt.query } });
        openHref = href;
      }
      window.open(openHref, '_blank');
    },
    closeWindow() {
      window.open('', '_parent', '').close();
    },
    refreshCurrentPage() {
      this.$router.go(0);
    },
  },
  mounted() {},
};
</script>
