import Vue from 'vue';
import { dateFormat } from './date-time-utils';

// 将数组形式的日期转成对应format
Vue.filter('localTimeFormat', (formatDate, fmt) => {
  if (formatDate == null || formatDate == '') {
    return '-';
  } else {
    if (formatDate.length == 5) {
      formatDate.push(0);
    }
    return dateFormat(formatDate.slice(0, 6), fmt);
  }
});

// dayjs 通用格式
Vue.filter('dateformat', dateFormat);

// 格式转换语言
Vue.filter('language', (val) => {
  switch (val) {
    case 'en':
      return 'English';
    case 'ro':
      return 'Romanian';
    case 'en_US':
      return 'English(United States)';
    case 'en_KE':
      return 'English (Kenya)';
    case 'sw_KE':
      return 'Swahili (Kenya)';
    case 'st':
      return 'Sesotho';
    default:
      return '-';
  }
});

// 空值
Vue.filter('formatEmpty', function (value, emptyStr = '-') {
  if (value) return value;
  return emptyStr;
});

Vue.filter('processBegin', (val) => {
  return val == undefined ? 0 : val;
});

Vue.filter('processEnd', (val) => {
  return val == undefined ? -1 : val;
});

Vue.filter('processInfi', (val) => {
  if (val == -1) {
    return '∞';
  }
  return val;
});

Vue.filter('toFixed', (val, len = 0) => {
  return Number(val).toFixed(len);
});

Vue.filter('toThousands', (num) => {
  return (num || 0).toString().replace(/(\d)(?=(?:\d{3})+$|(?:\d{3})+\.\d+)/g, '$1,');
});
