import { getAppRoutes } from '@/common/utils/menu';
import path from 'path';
import router, { resetRouter } from '@/router';
import { getOperPermission } from '@/views/home/api';
const TOP_MENUS = [
  'home',
  'taskCenter',
  'transactionCenter',
  'businessCenter',
  'markingCenter',
  'identifierCenter',
  'productCenter',
  'helpCenter',
];

const state = {
  menus: [],
  topMenus: [],
  activeMenu: '',
  sidebars: [],
  menuLoaded: false,
  mainRoutes: [],
  permCodes: JSON.parse(sessionStorage.getItem('perm_codes')) || [],
};

const mutations = {
  SAVE_USER_PERMS: (state, permissionList) => {
    state.permCodes = permissionList;
    sessionStorage.setItem('perm_codes', JSON.stringify(permissionList || []));
  },
  SET_MENUS: (state) => {
    const { mainRoutes, activeMenu } = state;
    state.menus = initMenus(mainRoutes, activeMenu);
  },
  SET_TOP_MENUS: (state, topMenus) => {
    state.topMenus = topMenus;
  },
  SET_ACTIVE_MEUNS: (state, activeMenu) => {
    state.activeMenu = activeMenu;
  },
  SET_SIDEBARS: (state, sidebars) => {
    state.sidebars = sidebars;
  },
  SET_MENULOADED: (state, payload) => {
    state.menuLoaded = payload;
  },
  SET_MAINROUTES: (state, mainRoutes) => {
    state.mainRoutes = mainRoutes;
  },
};

const actions = {
  setMeuns({ commit }, { activeMenu }) {
    commit('SET_ACTIVE_MEUNS', activeMenu);
    commit('SET_MENUS');
  },
  setTopMenus({ commit }) {
    commit('SET_MENULOADED', true);
    return new Promise((resolve, reject) => {
      getAppRoutes().then(
        (res) => {
          let { mainRoutes } = res;
          let topMenus = mainRoutes.map((cm) => {
            let path = cm.path ? cm.path.replace('/', '') : '';
            let menus = initMenus(mainRoutes, path) || [];
            menus = menus.filter((mf) => mf.children.length > 0);
            let disabled = !menus.length;
            let pageRoute = getFirstRoute(menus) || '/';
            if (path === 'home') {
              disabled = cm.meta.disabled;
              pageRoute = cm.path;
            }
            return {
              ...cm.meta,
              disabled,
              path: pageRoute,
              basePath: path,
            };
          });
          topMenus = topMenus.filter((tf) => TOP_MENUS.includes(tf.basePath));

          commit('SET_MAINROUTES', mainRoutes);
          commit('SET_TOP_MENUS', topMenus);
          commit('SET_MENUS');
          // 动态加载路由
          resetRouter();
          router.addRoutes([
            {
              path: '/mainPage',
              redirect: '/home',
              component: () => import('@/layout'),
              children: [...mainRoutes],
            },
          ]);
          resolve();
        },
        (err) => {
          reject(err);
        }
      );
    });
  },
  getUserPermData({ commit }) {
    return new Promise((resolve, reject) => {
      getOperPermission({})
        .then((res) => {
          if (res.header && res.header.success) {
            const permissionList = res.permissionList || [];
            commit('SAVE_USER_PERMS', permissionList);
            resolve(permissionList);
          }
        })
        .catch((res) => {
          reject(res);
        });
    });
  },
  setSidebars({ commit }, sidebars) {
    commit('SET_SIDEBARS', sidebars);
  },
};

/**
 * 路由扁平化获取menus首条path
 */
function getFirstRoute(route) {
  let child = route[0] || {};
  if (child.children) {
    return getFirstRoute(child.children);
  }
  return child.path;
}

/**
 * 过滤没有子菜单的二级路由
 */
function initMenus(mainRoutes, path) {
  let menus = initRouter(mainRoutes, path) || [];
  return menus.filter((mf) => mf.children.length > 0);
}

function initRouter(mainRoutes, activeMenu) {
  let activeMenus = mainRoutes.filter((mf) => mf.path === activeMenu);
  if (!activeMenus[0]) return;
  let { children, path: basePath, hidden = false } = activeMenus[0] || {};
  basePath = path.resolve('/mainPage', basePath);
  let childMenus = [];
  if (children && !hidden) {
    childMenus = children.map((router) => {
      let meta = router.meta || {};
      let routerBasePath = basePath + '/' + router.path;
      let meunObj = {
        ...meta,
        path: router.path,
        children: [],
        collapse: router.collapse || false,
      };
      if (router.children) {
        let children = router.children.filter((cf) =>
          cf.children ? cf.children.some((cs) => !cs.hidden) : !cf.hidden
        );
        meunObj.children = children.map((rm) => {
          return {
            ...rm.meta,
            path: `${routerBasePath}/${rm.path}${rm.redirect ? '/' + rm.redirect : ''}`,
          };
        });
      }
      return meunObj;
    });
  }
  return childMenus;
}

function splicingPath(route = [], pathStr = '') {
  let routeChild = route[0];
  if (routeChild) {
    let routePath = routeChild.path;
    pathStr += /^\//.test(routePath) ? routePath : `/${routePath}`;
    if (routeChild.children) {
      return splicingPath(routeChild.children, pathStr);
    } else {
      return pathStr || '';
    }
  } else {
    return pathStr;
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters: {
    permCodes: (state) => state.permCodes,
    menuLoaded: (state) => state.menuLoaded,
  },
};
