import request from '@/common/utils/request';

// 在线修改密码
export function changePasswordOnline(data) {
  return request({
    url: '/fintech/operator/v1/changePasswordOnline',
    method: 'post',
    data: data,
  });
}

// 密码校验组织操作员身份
export function authenticate(data) {
  return request({
    url: '/v1/authenticate',
    method: 'post',
    data: data,
  });
}

// 修改组织操作员的QA
export function changeOperatorQA(data) {
  return request({
    url: '/fintech/operator/v1/changeQA',
    method: 'post',
    data: data,
  });
}

// 获取操作员信息
export function getOrgOperatorDetail(customerId = '') {
  return request({
    url: `/fintech/v1/orgOperator/detail?receiverIdentityId=${customerId}`,
    method: 'get',
  });
}

// 修改密码
export function changeOrgOperatorPin(data) {
  return request({
    url: 'fintech/v1/orgOperator/changeOrgOperatorPin',
    method: 'post',
    data,
  });
}

// 获取操作权限
export function getOperPermission(data) {
  return request({
    url: '/fintech/identity/v1/queryPermission',
    data: data,
    method: 'post',
  });
}

// 校验当前PIN
export function checkCurrentPin(data) {
  return request({
    url: 'fintech/v1/orgOperator/checkCurrentPin',
    data: data,
    method: 'post',
    ignoreError: true,
  });
}

// 获取newPin校验规则
export function queryNewPinRule() {
  return request({
    url: 'fintech/v1/orgOperator/queryNewPinRule',
    method: 'get',
  });
}
