import { Message, MessageBox, Notification, Loading } from 'element-ui';

let loadingInstance;

export default {
  // 消息提示
  msg(message) {
    if (document.getElementsByClassName('el-message--info').length === 0) Message.info(message);
  },
  // 错误消息
  msgError(message) {
    if (document.getElementsByClassName('el-message--error').length === 0) Message.error(message);
  },
  // 成功消息
  msgSuccess(message) {
    if (document.getElementsByClassName('el-message--success').length === 0) Message.success(message);
  },
  // 警告消息
  msgWarning(message) {
    if (document.getElementsByClassName('el-message--warning').length === 0) Message.warning(message);
  },
  // 弹出提示
  alert(message) {
    if (document.getElementsByClassName('el-alert--info').length === 0) MessageBox.alert(message, 'System Prompt');
  },
  // 错误提示
  alertError(message) {
    if (document.getElementsByClassName('el-alert--error').length === 0)
      MessageBox.alert(message, 'System Prompt', {
        type: 'error',
        confirmButtonText: 'Confirm',
      });
  },
  // 成功提示
  alertSuccess(message) {
    if (document.getElementsByClassName('el-alert--success').length === 0)
      MessageBox.alert(message, 'System Prompt', { type: 'success' });
  },
  // 警告提示
  alertWarning(message) {
    if (document.getElementsByClassName('el-alert--warning').length === 0)
      MessageBox.alert(message, 'System Prompt', { type: 'warning' });
  },
  // 提示通知
  notify(message) {
    Notification.info(message);
  },
  // 通知错误
  notifyError(message) {
    Notification.error(message);
  },
  // 通知成功
  notifySuccess(message) {
    Notification.success(message);
  },
  // 通知警告
  notifyWarning(message) {
    Notification.warning(message);
  },
  // 确认窗体
  confirm(
    content,
    title = 'System Prompt',
    options = { confirmButtonText: 'Yes', cancelButtonText: 'No', type: 'warning' }
  ) {
    return MessageBox.confirm(content, title, options);
  },
  // 提交内容
  prompt(content) {
    return MessageBox.prompt(content, 'System Prompt', {
      confirmButtonText: 'Yes',
      cancelButtonText: 'No',
      type: 'warning',
    });
  },
  // 打开遮罩层
  loading(content) {
    loadingInstance = Loading.service({
      lock: true,
      text: content,
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)',
    });
  },
  // 关闭遮罩层
  closeLoading() {
    loadingInstance.close();
  },
};
