import Vue from 'vue';

const getDictUnit = () => {
  return {
    listWithAll: [],
    list: [],
    objMap: {},
  };
};

const getDefaultState = () => {
  return {};
};
const state = getDefaultState();

// 兼容数据库字典多种历史配置值
const ENABLE_VALUE_SET = ['Y', 'y', 'ON', 'on', 'true', 'TRUE', '1', undefined];

/**
 * 预处理后台返回字典数据
 *
 * @param {dataDictItemsVOList} dataDictItemsVOList
 */
function preprocessDictItems(dataDictItemsVOList, hideDisabledItem = false) {
  if (!dataDictItemsVOList) {
    return dataDictItemsVOList;
  }
  if (hideDisabledItem) {
    return dataDictItemsVOList.filter((item) => ENABLE_VALUE_SET.indexOf(item.status) != -1);
  }
  return dataDictItemsVOList;
}

const mutations = {
  RESET_STATE: (state) => {
    Object.assign(state, getDefaultState());
  },
  SET_DICT_LIST: (state, dictMapVo) => {
    let moduleName = dictMapVo.moduleName;
    let hideDisabledItem = dictMapVo.hideDisabledItem;
    delete dictMapVo.moduleName;
    delete dictMapVo.hideDisabledItem;

    for (let dictKey in dictMapVo) {
      let options = dictMapVo[dictKey];
      if (!options) {
        continue;
      }
      let dataDictItemsVOList = preprocessDictItems(options, hideDisabledItem);

      let dict = state[moduleName][dictKey];
      if (!dict) {
        dict = getDictUnit();
        Vue.set(state[moduleName], dictKey, dict);
      }
      dict.list = dataDictItemsVOList || [];
      dict.listWithAll = [{ value: null, label: 'All', status: 'Y', enable: true }, ...dict.list];
      dict.objMap = {};

      for (let item of dataDictItemsVOList || []) {
        dict.objMap[item.value] = item.label;
      }
    }
  },
};

function getRequestParam(dictCodes = []) {
  return {
    dictCodes,
  };
}

const actions = {
  resetState({ commit }) {
    commit('RESET_STATE');
  },

  /**
   * 查询服务字典支持隐藏未启用的字典项，默认不开启该功能，hideDisabledItem为true时开启
   *
   * @param dictCodes 待查询字典列表
   * @param hideDisabledItem 是否隐藏未启用字典
   */
  getModuleDicts({ commit, state }, { moduleName, dictCodes = [], hideDisabledItem = true, queryAPI = () => {} }) {
    return new Promise((resolve, reject) => {
      if (!state[moduleName]) {
        Vue.set(state, moduleName, {});
      }
      dictCodes.forEach((dictCode) => {
        Vue.set(state[moduleName], dictCode, getDictUnit());
      });
      let dictCodes2Query = [...dictCodes];
      queryAPI(getRequestParam(dictCodes2Query))
        .then((res) => {
          if (!res.header || !res.header.success) {
            reject(res);
          }
          commit('SET_DICT_LIST', { ...(res.dictDetailsMap || {}), moduleName, hideDisabledItem });
          resolve('success');
        })
        .catch((error) => {
          reject(error);
        });
    });
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters: {
    moduleDict: (state) => state,
  },
};
