import Vue from 'vue';
import router from '@/router';
import { MessageBox as messageBox, Message } from 'element-ui';
import { isEqual } from 'lodash';

function noRepeatFunc(fn, delay) {
  let prevArgs = null;
  let lastTime = Date.now();

  return function (args) {
    const now = Date.now();
    // 如果时间很短而且responseCode相同，当成同一个message只弹一次
    if (isEqual(prevArgs, args) && now - lastTime < delay * 1000) {
      return;
    }
    fn(args);
    prevArgs = args;
    lastTime = now;
  };
}

function createMessage(args) {
  const vm = new Vue();
  const h = vm.$createElement;
  let res = args;

  let vNode = null;

  if (typeof args === 'string') {
    vNode = [
      h('div', { class: 'icon-alart icon-error', style: 'flex: initial;' }, null),
      h('div', null, [h('p', { class: 'msg-value' }, args ? args : '')]),
    ];
  } else {
    vNode = [
      h('div', { class: 'icon-alart icon-error', style: 'flex: initial;' }, null),
      h('div', null, [
        h('div', { class: 'msg-label' }, 'Exception Code :'),
        h('p', { class: 'msg-value' }, res.header ? res.header.responseCode : ''),
        h('div', { class: 'msg-label' }, 'Exception Description :'),
        h('p', { class: 'msg-value' }, res.header ? res.header.responseDesc : ''),
        h('div', { class: 'msg-label' }, 'Exception Cause :'),
        h('p', { class: 'msg-value' }, res.header ? res.header.errorCause : ''),
        h('div', { class: 'msg-label' }, 'Solution :'),
        h('p', { class: 'msg-value' }, res.header ? res.header.errorSolution : ''),
      ]),
    ];
  }
  return h('div', { style: 'display: flex' }, vNode);
}

function emitMsgBox(args) {
  const timeout = (args.header ? args.header.responseCode : '') === 'FE72300000';
  const message = createMessage(args);

  return messageBox({
    title: 'Error',
    message,
    showConfirmButton: true,
    closeOnClickModal: false,
    confirmButtonText: timeout ? 'Login Again' : 'Confirm',
  }).then(() => {
    if (timeout) {
      const currentPath = (router.history.pending && router.history.pending.path) || '';
      router.push({ path: '/login', query: { redirectPath: currentPath } });
    }
  });
}

export function emitMessageInfo(content) {
  if (document.getElementsByClassName('el-message--info').length === 0) {
    return Message.info(content);
  }
}

export default noRepeatFunc(emitMsgBox, 0.5);
