export function buildUrlWithObj(url, data) {
  if (!data) {
    return url;
  }
  url += '?';
  for (let key in data) {
    if (!data[key]) {
      continue;
    }
    url = url + `${key}=${encodeURIComponent(data[key])}&`;
  }
  if (url.endsWith('?') || url.endsWith('&')) {
    url = url.substring(0, url.length - 1);
  }
  return url;
}

export function getQueryParams(url) {
  let rawParams = url.split('&');
  let params = {};
  for (let rawParam of rawParams) {
    let splitStr = rawParam.split('=');
    let paramName = splitStr[0];
    params[paramName] = splitStr[1];
  }
  return params;
}
