import cookie from '@/common/utils/cookie';

const state = {
  logoutVisible: false,
  isKycFormChange: {},
  username: cookie.get('operator_name') || '',
};

const mutations = {
  SET_LOGOUT_VISIBLE: (state, logoutVisible) => {
    state.logoutVisible = logoutVisible;
  },
  SET_KYCFORM_CHANGE: (state, isChagne) => {
    state.isKycFormChange = { ...isChagne };
  },
  SET_CURRENT_USER: (state, payload) => {
    state.username = payload;
  },
};

const actions = {
  setLogoutVisible({ commit }, logoutVisible) {
    commit('SET_LOGOUT_VISIBLE', logoutVisible);
  },
  setCurrentUser({ commit }, username) {
    commit('SET_CURRENT_USER', username);
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
