import Vue from 'vue';
import App from './App';
import ElementUI from 'element-ui';
import router from './router';
import store from './store';
import i18n from './i18n';
import mixins from './mixins';
import '@/assets/icons';

import 'element-ui/lib/theme-chalk/index.css';
// elementUI主题样式
import '@/assets/theme/index.css';
import GlobalStyleVariables from '@/assets/css/variables.scss';
import plugins from '@/common/plugin';
import 'multi-items-input';
import 'multi-items-input/dist/multi-items-input.css';

Vue.prototype.$EventBus = new Vue();
Vue.prototype.GlobalStyleVariables = GlobalStyleVariables;
// 引用过滤器、指令
require('@/common/utils/filter');
require('@/common/utils/prevent-re-click.js');
// 实现国际化切换
Vue.use(ElementUI, {
  i18n: (key, value) => i18n.t(key, value),
});
Vue.use(plugins);

Vue.mixin(mixins);
import '@/assets/css/index.scss';

// elementUI主题样式
import 'element-ui/lib/theme-chalk/index.css';
Vue.config.productionTip = false;
new Vue({
  el: '#app',
  i18n,
  router,
  store,
  render: (h) => h(App),
});
