// 动态菜单集中处理
import { getAppMenus } from '@/views/auth/api';
import { mainRoutes } from '@/router/main-type';

const MAIN_MENU_CODES = [
  'HOME',
  'TASKCENTER',
  'TRANSACTIONCENTER',
  'BUSINESSCENTER',
  'MARKINGCENTER',
  'IDENTIFIERCENTER',
  'PRODUCTCENTER',
  'HELPCENTER',
  'PERSONALSETTINGS',
];

// 将返回的菜单映射到不同的category： mainRoutes、createTypeRoutes、configTypeRoutes
const mapMenuCodeToCategory = (menus) => {
  const mainMenus = menus.filter((menu) => MAIN_MENU_CODES.includes(menu.menuCode));
  return { mainMenus };
};

// 只有菜单中有的路由才能加载，在这里过滤
function filterRoutesFromMenu(routes, menus) {
  const ret = [];
  routes.forEach((route) => {
    const newRoute = { ...route };
    const menuItem = menus.find((item) => item.menuUrl === route.path);
    if (menuItem) {
      if (menuItem.meta) {
        newRoute.meta = { ...newRoute.meta, ...menuItem.meta };
      }
      if (menuItem.children && newRoute.children) {
        const children = filterRoutesFromMenu(newRoute.children, menuItem.children);
        newRoute.children = children;
      }
      ret.push(newRoute);
    }
  });
  return ret;
}

// 从api拿回菜单并过滤出要显示的菜单
export function getAppRoutesFromApi() {
  return new Promise((resolve, reject) => {
    getAppMenus().then((res) => {
      if (res && res.header && res.header.success) {
        const { mainMenus } = mapMenuCodeToCategory(res.menus);
        const filterMainRoutes = filterRoutesFromMenu(mainRoutes, mainMenus);
        resolve({
          mainRoutes: filterMainRoutes,
        });
      } else {
        if (res && res.header && ['FE72300000'].includes(res.header.responseCode)) {
          // 由于登录过期的不同时报错
          resolve({
            mainRoutes: [],
          });
        } else {
          if (['FE72300000', 'FE72200009'].includes(res.header.responseCode)) return;
          reject(new Error('get menus failed'));
        }
      }
    });
  });
}

export function getAllAppRoutes() {
  return new Promise((resolve) => {
    resolve({ mainRoutes });
  });
}

export function getAppRoutes() {
  if (process.env.VUE_APP_MENU_FROM_API === '1') {
    return getAppRoutesFromApi();
  }
  return getAllAppRoutes();
}

// routes里有很多菜单并不需要的东西，不必保存在store里占用内存
export function getMenusFromRoutes(routes) {
  if (!routes) return [];
  const res = [];
  routes.forEach((route) => {
    let { component, ...rest } = route;
    const menu = { ...rest };
    if (route.children) {
      menu.children = getMenusFromRoutes(route.children);
    }
    res.push(menu);
    component = null;
  });
  return res;
}

// 输出全量菜单给后台 start
function getRouteNameFrom(route) {
  if (route.name) return route.name;
  if (route.path) {
    if (route.path.startsWith('/')) {
      return route.path.slice(1);
    }
    return route.path;
  }
}

function createMenu(routes, parent) {
  const parentMenuCode = parent ? parent.menuCode : '';
  const menus = [];

  routes.forEach((route, index) => {
    const menuCode = (parentMenuCode ? parentMenuCode + '_' : '') + getRouteNameFrom(route);
    const menu = {
      menuCode: menuCode.toUpperCase(),
      menuName: menuCode.toUpperCase(),
      menuUrl: route.path,
      displayOrder: index + 1,
      parentMenuCode: parentMenuCode,
      permCode: '',
    };
    if (route.children) {
      const children = createMenu(route.children, { ...menu });
      menu.children = children;
    } else {
      menu.children = [];
    }
    menus.push(menu);
  });
  return menus;
}

export async function generateAllRouteConfig() {
  const routes = [...mainRoutes];
  const menus = createMenu(routes);
  window.localStorage.setItem('routeConfig', JSON.stringify(menus));
}

// 输出全量菜单给后台 end
