/*
 * 路由对象模块
 * */
import Vue from 'vue';
import VueRouter from 'vue-router';
import { changePwdRutes } from './change-password';
import cookie from '@/common/utils/cookie';
import store from '@/store';

const whiteList = ['/login', '/forgetPwd', '/forceChangePwd', '/firstLoginChangePwd'];

// 处理防止连续点击路由报错
let routerPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch((err) => err);
};

// 安装插件
Vue.use(VueRouter);

// 定义 (路由) 组件
const routes = [
  {
    path: '/',
    redirect: '/login',
  },
  {
    path: '/index',
    component: () => import('@/views/index/index'),
  },
  {
    path: '/login',
    component: () => import('@/views/auth/login/index'),
  },
  ...changePwdRutes,
];

function createNewRouter() {
  return new VueRouter({
    mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes,
  });
}

export function resetRouter() {
  const newRouter = createNewRouter();
  router.matcher = newRouter.matcher; // reset router
}

const router = createNewRouter();

router.beforeEach(async (to, from, next) => {
  let uToken = cookie.get('o-token');
  if (uToken) {
    const menuLoaded = store.state.routerStore.menuLoaded;
    if (!menuLoaded && whiteList.indexOf(to.path) === -1) {
      try {
        await store.dispatch('routerStore/setTopMenus');
        next({ ...to, replace: true });
      } catch (error) {
        const vm = router.app;
        vm.$message.error(error.message || 'Get menus failed!');
        next();
      }
    } else {
      next();
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next();
    } else {
      next('/login');
    }
  }
});

router.afterEach((to) => {
  if (to.meta.title) {
    const title = router.app.$t(to.meta.title);
    document.title = `${title} | Organization Portal`;
  }
});

export default router;
