import moment from 'dayjs';

// 支持array格式

var arraySupport = require('dayjs/plugin/arraySupport');
var customParseFormat = require('dayjs/plugin/customParseFormat');

moment.extend(arraySupport);
moment.extend(customParseFormat);

/**
 * dayjs格式化数组格式的日期时认定传入的月份是以0开始的，实际是以1，所以这里要减一
 *
 * @param {时间字符串或者时间数组} dateStr 
 */
function formartArrayTime(dateStr) {
  if (dateStr instanceof Array && dateStr[1]) {
    dateStr[1] = dateStr[1] - 1;
  }
  return dateStr;
}

/**
 *
 * @export
 * @param {*} dateStr 待格式化日期字符串
 * @param {string} [format='DD/MM/YYYY'] 目标格式
 * @param {*} orginalFormat 原格式
 * @returns
 */
export function dateFormat(dateStr, format = 'DD/MM/YYYY', orginalFormat) {
  if (!dateStr) return '';
  dateStr = formartArrayTime(dateStr);
  let dateObj;
  if (orginalFormat) {
    dateObj = moment(dateStr, orginalFormat);
  } else {
    dateObj = moment(dateStr);
  }
  if (!dateObj.isValid()) {
    return 'Invalid Date';
  }
  return dateObj.format(format);
}

export function formatDateStr(value) {
  // 日期字符串格式转换，yyyyMMdd -> dd-MM-yyyy
  return dateFormat(value, 'DD-MM-YYYY', 'YYYYMMDD');
}

export function formatStrToDate(dateStr) {
  dateStr = formartArrayTime(dateStr);
  // 字符串转日期，yyyyMMdd
  if (dateStr === null || dateStr === '' || dateStr === undefined) {
    return null;
  }
  return moment(dateStr, 'YYYYMMDD').toDate();
}

export function formatStrToDate2(dateStr) {
  dateStr = formartArrayTime(dateStr);
  // 字符串转日期，dd-MM-yyyy
  if (dateStr === null || dateStr === '' || dateStr === undefined) {
    return null;
  }
  return moment(dateStr, 'DD-MM-YYYY').toDate();
}

export function formatStrToDate3(dateStr) {
  dateStr = formartArrayTime(dateStr);
  // 字符串转日期，dd-MM-yyyy
  if (dateStr === null || dateStr === '' || dateStr === undefined) {
    return null;
  }
  return new Date(dateStr.replace(/^(\d{2})-(\d{2})-(\d{4})$/, '$3-$2-$1'));
}

export function formatDateTimeSrv(date) {
  return dateFormat(date, 'YYYYMMDD');
}

export function formatDateTimeStr(value) {
  // 日期字符串格式转换，yyyyMMddHHmmss -> dd-MM-yyyy HH:mm:ss
  return dateFormat(value, 'DD-MM-YYYY HH:mm:ss', 'YYYYMMDDHHmmss');
}

export function formatDateTimeStr2(value) {
  // 日期字符串格式转换，yyyy-MM-dd HH:mm:ss -> dd-MM-yyyy HH:mm:ss
  return dateFormat(value, 'DD-MM-YYYY HH:mm:ss', 'YYYY-MM-DD HH:mm:ss');
}

export function formatDateTimeStr3(value) {
  // 日期字符串格式转换，yyyyMMddHHmmss -> dd/MM/yyyy HH:mm:ss
  return dateFormat(value, 'DD/MM/YYYY HH:mm:ss', 'YYYYMMDDHHmmss');
}

export function formatDateFromArray(original, format) {
  if (original && original.length) {
    let arr = [...original];
    if (original.length === 5) {
      arr.push(0);
    }

    if (arr[1]) {
      arr[1] = arr[1] - 1;
    }
    return dateFormat(arr.slice(0, 6), format);
  }
  return '-';
}
